  import React from "react";
  import { Card, CardBody, CardHeader } from "react-bootstrap";
  import { baseUrl } from "../../../../../utils";
  import TrueSenseSkeleton from "../../layout/skeleton/trueSenseSkeleton";
import { Link } from "react-router-dom";
import moment from "moment";

  const TrueSense = (props) => {
  const { truSense, loader, dateFilter, selectedWallFeed } = props;

  return (
    <>
      <Card className="sw-card-custom gutter-b card-shadow mb-5">
        <CardHeader className="p-5 d-flex align-items-center justify-content-between">
          <h6 class="card-label font-weight-bolder d-flex align-items-center m-0"><img className="me-2" src={baseUrl(`dashboard/analytic/ai.svg`)} height={"20px"} width={"20px"}/> <span>TrueSense Recommendation</span></h6>
        </CardHeader>
        <CardBody className="p-5">
           {loader ? <TrueSenseSkeleton/>
            : truSense && Object.keys(truSense).length
            ? truSense.map((data,key) => { 
                return (
                  <Link className="mb-4 mb-lst-0 d-block" to={selectedWallFeed.length ? `/moderation/index/${localStorage.getItem('wallId')}?recommendation=${data.name}/${moment(dateFilter.startDate).format("YYYY-MM-DD")}/${moment(dateFilter.endDate).format("YYYY-MM-DD")}` : `#`}>  
                    <div className="p-6 border rounded">
                      <div className="d-flex align-items-center">
                        <div className="d-flex align-items-start me-10 w-100">
                          <div className="border rounded p-2 me-4">
                            <span className={`bulits ${key==0 ? 'bg-success' : key==1 ? 'bg-warning' : 'bg-danger' }  d-block rounded-circle`}></span>
                          </div>
                          <div className="">
                            <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name font-size-lg">
                              { data.name }
                            </p>
                            <p className="text-dark mb-0">
                              {data.details}
                            </p>
                          </div>
                        </div>
                        <p className="font-weight-boldest text-dark display-6 m-0">
                          { loader ? 0 : data.count}
                        </p>
                      </div>
                   </div>
                 </Link>  
                );
              })
            : null}
            
        </CardBody>
      </Card>
    </>
  );
};

  export default React.memo(TrueSense);
